<script>
export default {
	name: 'Showcase',
	data() {
		return {
			lightModeToggled: localStorage.getItem('light-mode'),
		};
	},
	methods: {
		toggleLightMode() {
			let lightMode = localStorage.getItem('light-mode');

			if (lightMode !== 'enabled') {
				this.enableLightMode();
			} else {
				this.disableLightMode();
			}
		},
		enableLightMode() {
			this.lightModeToggled = !this.lightModeToggled;
			document.body.classList.add('light-mode');
			localStorage.setItem('light-mode', 'enabled');
		},
		disableLightMode() {
			this.lightModeToggled = !this.lightModeToggled;
			document.body.classList.remove('light-mode');
			localStorage.setItem('light-mode', null);
		},
	},
	mounted() {
		if (localStorage.getItem('light-mode') === 'enabled') {
			this.enableLightMode();
		}
	},
};
</script>

<style lang="scss" scoped>
.showcase {
	position: relative;
	width: 80%;
	max-width: 120rem;
	height: 100%;
	margin: 0 auto;

	display: grid;
	grid-template-columns: repeat(12, 1fr);
	grid-template-rows: repeat(6, 1fr);

	@media only screen and(max-width:$vp-8) {
		width: 90%;
	}
}

.left {
	grid-column: 1 / 8;
	grid-row: 2 / 5;

	display: flex;
	flex-direction: column;
	justify-content: center;

	animation: down 1s ease-in-out forwards;
	z-index: 10;
	@media only screen and(max-width:$vp-9) {
		grid-row: 1 / 5;
		grid-column: 1 / -1;
		align-items: center;
		text-align: center;
	}

	& > *:not(:last-child) {
		margin-bottom: 1.5rem;
	}
	.desc {
		width: 75%;
		max-width: 35rem;
		color: var(--font-secondary);
	}
	.buttons {
		margin-top: 1.5rem;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		.btn-sec {
			transition: background 350ms ease-in-out;
			margin-left: 2rem;
		}
	}
}
.right {
	grid-column: 5 / -1;
	grid-row: 1 / -1;
	align-self: center;

	width: 100%;

	svg {
		transform: scale(1.1);
		width: 100%;
		z-index: 1;
	}
	#rest,
	#desk,
	#man,
	#laptop {
		opacity: 0;
	}
	#rest,
	#desk {
		animation: right 1s ease forwards 1s;
	}
	#man {
		animation: right 500ms ease forwards 1500ms;
	}
	#laptop {
		animation: right 500ms ease forwards 1500ms;
	}
	@media only screen and(max-width:$vp-9) {
		transform: scale(1.1);

		justify-self: center;
		grid-row: 3 / -1;
		grid-column: 1 / -1;
	}
}
.toggleLightMode {
	z-index: 50;
